.onescreen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    @include sectionOverlay(0.3);

    &__inner {
        @include flexColumn;
        justify-content: center;
        align-items: center;
        padding: 0 10px;
        flex: 1;

        @media (orientation: landscape) {
            flex-direction: row;
            width: 100%;
        }

        @include respond-min(md) {
            @include flexColumn;
            width: auto;
        }
    }

    .screen {

        &__inner {
            display: flex;
            flex-direction: column;
            justify-content: center;

            @media (orientation: landscape) {
                margin: auto;
            }

            @include respond-min(md) {
                margin: 0;
            }
        } //.screen__inner

        &__title {
            font-size: 30px;
            color: #fff;
        } //.screen__title

        &__desc {
            margin-bottom: 20px;
            font-size: 20px;
            color: #fff;
        } //.screen__desc
    } //.screen

    .form {
        height: 280px;
        padding: 30px;
        margin: 0;
        background-color: rgba(255,255,255, 0.4);

        @media (orientation: landscape) {
            margin: auto;
        }

        @include respond-min(md) {
            margin: 0;
        }
    }

    .footer {
        display: flex;
        justify-content: center;
        background-color: rgba(255,255,255, 0.4);
    }

    .icon-list {
        margin-left: 0;
        margin-right: 0;

        &__icon {
            width: 50px;
            height: 50px;
            margin-bottom: 0;
            margin-right: 0;

            @include respond-min(xs) {
                width: 50px;
                height: 50px;
                margin-bottom: 0;
            }

            @include respond-min(sm) {
                margin-bottom: 0;
            }

            @include respond-min(md) {
                width: 50px;
                height: 50px;
                margin-bottom: 0;
            }

            &:hover {
                color: $color-main;
                border-color: $color-main;
                cursor: pointer;
            }

            & + .icon-list__icon {
                margin-left: 20px;

                @include respond-min(md) {
                    margin-left: 50px;
                }
            }

            i {
                font-size: 20px;

                @include respond-min(sm) {
                    font-size: 25px;
                }

                @include respond-min(md) {
                    font-size: 20px;
                }
            }

            .fa-phone {
                transform: scale(-1,1);
                font-size: 17px;

                @include respond-min(sm) {
                    font-size: 20px;
                }

                @include respond-min(md) {
                    font-size: 17px;
                }
            }

            .fa-instagram {
                font-size: 22px;

                @include respond-min(sm) {
                    font-size: 27px;
                }

                @include respond-min(md) {
                    font-size: 22px;
                }
            }
        } //.icon-list__icon
    } //.icon-list
} //.onescreen


