.footer {
    background-color: $color-bg--lighten;
    border-top: 0;

    @include respond-to(sm) {
        padding: 15px 0;
    }

    &__logo {
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: baseline;
    } //.footer__logo

    .logo {
        margin-left: 7px;
        flex: initial;

        &__link {
            color: $gray--darkest;
        }
    } //.footer .logo
} //.footer
